* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background-color: black;
}
.app {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-items: center;
  width: 100%;
  height: 100vh;
}

input {
  padding: 15px 20px;
  outline: none;
  border: 2px solid black;
  border-radius: 5px;
  transition: 0.3s;
}
input:focus {
  border: 2px solid blue;
}
button {
  padding: 6px 10px;
  background-color: #fff;
  outline: none;
  border: 2px solid black;
  border-radius: 5px;
  margin: 2px 0;
  transition: 0.3s;
}
button:hover {
  box-shadow: 0 0 10px black;
  color: black;
}

@media (max-width: 400px) {
  .app {
    height: 1000px;
  }
  .playerAndChat {
    flex-direction: column;
  }
}
